"use strict"

import * as path from "path"
import * as cp from "child_process"
import ChildProcess = cp.ChildProcess

import CodefeelClient from "./cfClient"

import * as vscode from "vscode"
import { outputChannel } from "./extension"

export default class DiagnosticsProvider {
  private static commandId: string = "codefeel.runDiagnostics"
  private command!: vscode.Disposable
  private diagnosticCollection!: vscode.DiagnosticCollection
  private client: CodefeelClient

  constructor(client: CodefeelClient) {
    this.client = client
  }

  public activate(subscriptions: vscode.Disposable[]) {
    this.command = vscode.commands.registerCommand(
      DiagnosticsProvider.commandId,
      this.provideCodeActions,
      this
    )
    subscriptions.push(this)
    this.diagnosticCollection = vscode.languages.createDiagnosticCollection(
      "code-feel"
    )

    vscode.workspace.onDidOpenTextDocument(
      this.getDiagnostics,
      this,
      subscriptions
    )
    vscode.workspace.onDidCloseTextDocument(
      (textDocument) => {
        this.diagnosticCollection.delete(textDocument.uri)
      },
      null,
      subscriptions
    )

    vscode.workspace.onDidSaveTextDocument(this.getDiagnostics, this)

    subscriptions.push(
      vscode.window.onDidChangeActiveTextEditor((editor) => {
        if (editor) {
          setTimeout(this.getDiagnostics, 500)
        }
      })
    )

    subscriptions.push(
      vscode.window.onDidChangeActiveTextEditor((editor) => {
        if (editor) {
          setTimeout(this.getDiagnostics, 500)
        }
      })
    )
  }

  public dispose(): void {
    this.diagnosticCollection.clear()
    this.diagnosticCollection.dispose()
    this.command.dispose()
  }

  private getDiagnostics(doc: vscode.TextDocument) {
    if (doc !== undefined) {

      const diagnostics = vscode.languages.getDiagnostics(doc.uri)
      // this.diagnosticCollection.set(doc.uri, diagnostics);
      if (diagnostics.length > 0) {
        console.log(`Sending diagnostics ${diagnostics.length}`)
        this.client.sendEvent({
          type: "diagnostics",
          diagnostics: diagnostics,
          file: doc.uri.toString()
        })
      }
    }
  }

  // todo
  public provideCodeActions(
    document: vscode.TextDocument,
    range: vscode.Range,
    context: vscode.CodeActionContext,
    token: vscode.CancellationToken
  ): vscode.Command[] {
    console.log("Not implemented")
    outputChannel.appendLine("Not implemented")
    let diagnostic: vscode.Diagnostic = context.diagnostics[0]
    return [
      {
        title: "Accept CodeFeel Python suggestion",
        command: DiagnosticsProvider.commandId,
        arguments: [document, diagnostic.range, diagnostic.message],
      },
    ]
  }
}
