import * as vscode from "vscode"
import Config from "../config"
export default class TestButton {
  public static myStatusBarItem: vscode.StatusBarItem | undefined

  static init(subscriptions: vscode.Disposable[]) {
    this.myStatusBarItem = vscode.window.createStatusBarItem(
      vscode.StatusBarAlignment.Right,
      100
    )
    this.myStatusBarItem.text = `Install Tests`
    this.myStatusBarItem.command = "gaim.installTests"
    // this.myStatusBarItem.command = ""
    subscriptions.push(this.myStatusBarItem)
    this.myStatusBarItem.show()
  }
}
