import * as vscode from "vscode"
var fs = require("fs")
import CodefeelClient from "./cfClient"

export default class StatsProvider {
  private static commandId: string = "codefeel.runStats";
  private command!: vscode.Disposable
  private client: CodefeelClient

  constructor(client: CodefeelClient) {
    this.client = client
  }

  public activate(subscriptions: vscode.Disposable[]) {
    this.command = vscode.commands.registerCommand(
      StatsProvider.commandId,
      this.getFileStats,
      this
    )
    subscriptions.push(this)

    // vscode.workspace.onDidOpenTextDocument(
    //   this.getFileStats,
    //   this,
    //   subscriptions
    // );

    // vscode.workspace.onDidSaveTextDocument(this.getFileStats, this, subscriptions);
    // vscode.window.onDidChangeActiveTextEditor((editor) => {
    //   if (editor) {
    //     setTimeout(this.getFileStats, 500);
    //   }
    // }, this, subscriptions)


  }

  public dispose(): void {
    this.command.dispose()
  }

  private getFileStats(doc: vscode.TextDocument) {
    if (doc.uri.scheme !== "file") {
      return
    }
    const lineCount = doc.lineCount
    console.log(`Sending file stats ${doc.uri.fsPath}`)
    const stats = fs.statSync(doc.uri.fsPath)
    this.client.sendEvent({ stats: stats, lineCount: lineCount })
  }
}
