// import Config from "./config";

import Config from "./config"
import { outputChannel } from "./extension"

import axios, { CancelToken } from "axios"
import { STATUS_CODES } from "http"
import { CommentThreadCollapsibleState } from "vscode"
import host, { featuresEnabled } from "./host"
import { features } from "process"
const fs = require("fs")
const path = require("path")

export interface httpConfig {
  method?: string
  url: string
  headers: any
  data?: string
}

export default class CodefeelClient {
  host: string
  secret: string
  canvasToken: string
  public get gqlHost() {
    return `${Config.settings.config.host}/api/graphql`
  }
  constructor(host: string) {
    this.host = host
    this.secret = ""
    this.canvasToken = ""
  }

  setAuth(secret?: string, canvasToken?: string) {
    if (secret !== undefined) {
      this.secret = secret
    }
    if (canvasToken !== undefined) {
      this.canvasToken = canvasToken
    }
  }

  async startSession() {
    this.sendEvent({
      msg: "Session Started",
    })
  }
  async endSession() {
    this.sendEvent({
      msg: "Session Ended",
    })
  }
  async sendEvent(event: any) {
    if (Config.settings.user.secret === undefined || Config.settings.user.secret === "" || !Config.settings.session.online) {
      return
    }
    const url = `${Config.settings.config.host}/codefeel`
    const data = {
      source: "visual-studio-code-extension",
      sessionID: Config.settings.session.sessionID,
      assignment: Config.settings.config.assignment,
      course: Config.settings.config.course,
      payload: event,
    }

    const result = await this.post({
      method: "post",
      url: url,
      data: JSON.stringify(data),
      headers: {
        "x-cf-secret": Config.settings.user.secret,
        "x-cf-signature-256": "55555",
        "x-cf-event": "log",
        "Content-Type": "application/json",
      },
    })
    if (result.status === "ok") {
      return true
    }
    return false
  }

  async getAssignmentRubric(course: string, assignment: string) {
    const data = JSON.stringify({
      query: `{
  rubric(course_id: ${course}, assignment_id: ${assignment}) {
      id
      data {
          id
          description
          long_description
          points
          ratings {
              criterion_id
              points
              description

          }
      }
  }
}`,
      variables: {},
    })



    const url = this.gqlHost
    const result = await this.post({
      method: "post",
      url: url,
      data: data,
      headers: {
        "Content-Type": "application/json",
      },
    })

    return result



  }

  async submitForGrading(repo: string) {
    if (Config.settings.config.assignment === undefined) {
      return
    }
    console.log("submitting for grading ")
    const data = JSON.stringify({
      repo: Config.settings.user.repo,
      assignment: Config.settings.config.assignment,
      source: "visual-studio-code-extension",
    })
    const url = `${host}/codefeel`

    const result = await this.post({
      method: "post",
      url: url,
      data: data,
      headers: {
        "x-cf-secret": Config.settings.user.secret,
        "x-cf-signature-256": "55555",
        "x-cf-event": "submit",
        "Content-Type": "application/json",
      },
    })
    if (result.status === "ok") {
      return true
    }
    return false
  }

  async getLogs() {
    var data = JSON.stringify({
      query: `{
  logs {
    msg
  }
}`,
      variables: {},
    })
    const url = this.gqlHost
    const result = await this.post({
      method: "post",
      url: this.gqlHost,
      headers: {
        secret: this.secret,
        "Content-Type": "application/json",
      },
      data: data,
    })
    return result.data.logs
  }

  async confirmIdentity(
    repo: string,
    assignment: string,
    courseName: string,
    username: string,
    secret: string
  ) {
    // var data = 
    //       query: `query {
    //   confirmIdentity(repo: "${repo}", assignment: "${assignment}", course: "${courseName}", username: "${username}", secret: "${secret}")    
    // }`,
    //       variables: {},
    //     })
    return await this.post({
      url: `${host}/git/me`, data: JSON.stringify({ secret }), headers: {
        "Accept-Encoding": "gzip, deflate, br",
        "Content-Type": "application/json",
        "x-cf-secret": secret,
        Accept: "application/json",
        Connection: "keep-alive",
      },
    }).then(res => {
      console.log(res)
      if (res === "No response") {
        return false
      } else if (res === "OK" || res === STATUS_CODES.OK) {
        return true
      } else {
        return res.statusCode !== STATUS_CODES.OK
      }
    })
    // const url = this.gqlHost
    // const result = await this.post({
    //   method: "post",
    //   url: url,
    //   data: data,
    //   headers: {
    //     "Accept-Encoding": "gzip, deflate, br",
    //     "Content-Type": "application/json",
    //     Accept: "application/json",
    //     Connection: "keep-alive",
    //   },
    // })
    // if (result !== "No response") {
    //   return result.data.confirmIdentity
    // } else {
    //   return true
    // }
  }
  async registerRepo(
    course: string,
    repo: string,
    term: string,
    username: string
  ) {
    let data: any = JSON.stringify({
      githubId: username,
      repo,
      course,
      term
    })
    console.log(data)
    return await this.post({
      url: `${Config.settings.config.host}/api/register`,
      data,
      headers: {
        "Accept-Encoding": "gzip, deflate, br",
        "Content-Type": "application/json",
        Accept: "application/json",
        Connection: "keep-alive",
      },
    })
  }

  async download(url: string, path: string) {
    outputChannel.appendLine("Downloading..." + url + "  " + path)
    const writer = fs.createWriteStream(path)

    const response = await axios({
      url,
      method: "GET",
      responseType: "stream",
    })

    response.data.pipe(writer)

    return new Promise((resolve, reject) => {
      writer.on("finish", resolve)
      writer.on("error", reject)
    })
  }

  async get(config: httpConfig) {
    try {
      let response = await axios.get(config.url, { timeout: Config.timeout, headers: config.headers })
      return JSON.stringify(response.data)
    } catch (err) {
      console.log(err)
      return "Failure"
    }
  }
  async post(config: httpConfig) {
    // const source = CancelToken.source();
    console.log(config)
    // const timeout = setTimeout(() => {
    //   source.cancel()
    //   // Timeout Logic
    // }, 10000)
    const CancelToken = axios.CancelToken
    const source = CancelToken.source()
    const timeout = setTimeout(() => {
      source.cancel()
      return "No response"
      // Timeout Logic
    }, 10000)
    return await axios.post(config.url, config.data,
      { timeout: Config.timeout, headers: config.headers, cancelToken: source.token })
      .then(data => {
        console.log(data)
        clearTimeout(timeout)
        return data.data
      })
      .catch((thrown) => {
        if (axios.isCancel(thrown)) {
          console.log('Request canceled', thrown.message)
          return "No response"
        } else if (thrown.response !== undefined && thrown.response.status !== 426) {
          outputChannel.appendLine(`Error:  + ${thrown.message} + ${config.url}`)
          return "No response"
          // handle error
        }
        else {
          return
        }
      })
  }
}
