// The module 'vscode' contains the VS Code extensibility API
// Import the module and reference it with the alias vscode in your code below
import * as vscode from "vscode"
import DiagnosticsProvider from "./diagnostics"
import StatsProvider from "./statistics"
import VersionBar from "./statusbar/versionBar"
import Config, { client } from "./config"
import Commands from "./commands/commands"
import path = require("path")
import { getGitApi } from "./git"
import { window } from "vscode"
import SubmitButton from "./statusbar/submitButton"
import TestButton from "./statusbar/testButton"

let extensionIsActive = true
export const outputChannel = window.createOutputChannel("gaim")
let subscriptions: any
export async function activateButtons() {
  if (TestButton.myStatusBarItem === undefined && Config.settings.config.assignment === "Roll a Ball") {
    TestButton.init(subscriptions) // initalize the status bar on the bottom right
  }
}
export async function activate(context: vscode.ExtensionContext) {
  Config.startTime = new Date()
  subscriptions = context.subscriptions
  const git = await getGitApi()
  if (!git) {
    console.log("ERROR: No git enabled.")
    return
  }
  let init = async () => {
    outputChannel.appendLine("Git initialized, continuing..." + git.state)
    VersionBar.updateStatusBarItem("initalizing")
    let commands = new Commands()
    await commands.activate(context.subscriptions)
    await Config.initialize(git, context) // initialize the software versions, repo URL, student secret, etc
    VersionBar.init(context.subscriptions) // initalize the status bar on the bottom right

    // store.enabled = vscode.workspace.getConfiguration("gaim").enabled
    // Enable/disable the auto-commit watcher as the user
    // opens/closes Git repos, modifies their settings
    // and/or manually enables it via the command palette.
    if (Config.settings.session.online) {

      SubmitButton.init(subscriptions) // initalize the status bar on the bottom right
    }
    let stats = new StatsProvider(client) // file stats for event logging. listens for file editing events
    stats.activate(subscriptions)
    let diagnosticsProvider = new DiagnosticsProvider(client) // diagnostics (problems pane) for event logging. listens for file editing events
    diagnosticsProvider.activate(subscriptions)
    client.startSession()
    VersionBar.updateStatusBarItem()
  }
  if (git.state === "uninitialized") {
    let listener = git.onDidChangeState(async (state) => {
      console.log("State changed: ", state)
      await init()
      activateButtons()
      listener.dispose()
    })
    context.subscriptions.push(listener)
  } else {
    init()
  }
  // Check for a new version every 30 minutes
}

// this method is called when your extension is deactivated
export function deactivate() {
  if (extensionIsActive) {
    client.endSession()
  }
}
