/**
 * Simple object check.
 * @param item
 * @returns {boolean}
 */
export function isObject(item: any) {
  return (item && typeof item === 'object' && !Array.isArray(item))
}

/**
 * Deep merge two objects.
 * @param target
 * @param ...sources
 */
export function mergeDeep(target: any, ...sources: [any]): any {
  if (!sources.length) { return target }
  const source = sources.shift()

  if (isObject(target) && isObject(source)) {
    for (const key in source) {
      if (isObject(source[key])) {
        if (!target[key]) { Object.assign(target, { [key]: {} }) }
        mergeDeep(target[key], source[key])
      } else {
        Object.assign(target, { [key]: source[key] })
      }
    }
  }

  return mergeDeep(target, ...sources)
}

const OK = "OK"

interface ICheck {
  file: string[]
  checks: RegExp[] | any[]
  add: { [index: string]: any }
  type: "json" | "yaml"
  propertyPath: string[]
}
interface Session {
  curBranch: string
  sessionID: string
  canvasToken?: string
  online: boolean
}
interface ConfigVars {
  host: string
  org: string
  type: string
  course: string
  term: string
  assignment: string
  assignments: any[]
  slug: string
}

interface UserVars {
  githubID: string
  secret: string
  repo: string
  submission: string
  canvasID?: string
  debugLevel: number
  fixesApplied: {
    [index: string]: boolean
  }
  versions: {
    [index: string]: string
  }
}
enum ConfigTypes {
  config = 'config',
  user = 'user',
  session = 'session'
}
type ConfigUnion = 'config' | 'user' | 'session'

interface IConfig {
  [key: string]: ConfigVars | UserVars | Session
  "config": ConfigVars
  "session": Session
  "user": UserVars
}
// }


