import * as vscode from "vscode"
import Config from "../config"
export default class VersionBar {
  private static myStatusBarItem: vscode.StatusBarItem | undefined

  static init(subscriptions: vscode.Disposable[]) {
    this.myStatusBarItem = vscode.window.createStatusBarItem(
      vscode.StatusBarAlignment.Right,
      100
    )
    this.myStatusBarItem.text = `GaIM:v${Config.settings.user.versions["gaim.vsix"]} `
    subscriptions.push(this.myStatusBarItem)
  }

  public static updateStatusBarItem(status: string = ""): void {
    if (this.myStatusBarItem) {
      this.myStatusBarItem.text = `GaIM:v${Config.settings.user.versions['gaim.vsix']}|${status !== "" ? ":" + status : ""}${Config.settings.session.online ? "online" : "offline"}`
      this.myStatusBarItem.show()
    }
  }
}
