const emailRe = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
export function getConfigVars() {
  return JSON.parse(
    readFileSync(
      vscode.workspace.workspaceFolders?.[0].uri.fsPath.toString() +
      "/.ucf/vsc.json",
      "utf8"
    )
  )
}
/**
 * Promise that sleeps for ms.
 * @param {number} ms - The number of ms to wait to resolve.
 */
export function sleep(ms: number) {
  return new Promise((resolve) => {
    setTimeout(resolve, ms)
  })
}

export async function checkEnabled(git: GitAPI.GitAPI) {
  let commitAndPush = false
  let curBranch = git!.repositories![0].state.HEAD!.name!
  await updateConfigVars({ user: { curBranch } })
  let error = false
  if (curBranch === "main" || curBranch === "master") {
    outputChannel.appendLine(
      "On main branch " + git.repositories[0].state.workingTreeChanges
    )
    //   // await switchToBranch("working")
    // } else {
    // Verify that upstream is correctly set.
    try {
      const gApi = await getGitApi()
      const repo = gApi?.repositories[0]
      outputChannel.appendLine(
        "Checking upstream..." +
        repo?.state.HEAD?.upstream?.remote +
        ":" +
        repo?.state.HEAD?.upstream?.name
      )
      if (repo?.state.HEAD?.upstream === undefined) {
        // First, try to set upstream and then force push?
        outputChannel.appendLine("Pushing to origin/" + curBranch)
        repo?.push("origin", curBranch, true, ForcePushMode.Force)
        await git.repositories[0].setBranchUpstream(
          curBranch,
          `origin/${curBranch}`
        )
      }
    } catch (err) {
      error = true
      outputChannel.appendLine("Problem pulling: conflict\n" + err)
    }
  }
  activateButtons()
  return
}

function matches(uri: vscode.Uri) {
  return minimatch(uri.path, "**/*", { dot: true })
}

export async function getGitUsername(): Promise<string> {
  const repoExp = new RegExp(
    `https://.*github.com/.*?/((?:dig\d{4}.?)-.{3,4}-(?:0w61|\\w\\d)?)-(.+?)(?:.git)?$`,
    "i"
  )
  let git = await getGitApi()
  let changed = false
  if (git === undefined) {
    return ""
  }
  let username, userEmail
  try {
    username = await git.repositories[0].getConfig("user.name")
  } catch (err) {
    console.log(err)
  }
  try {
    if (username === "" || username === undefined) {
      username = await git.repositories[0].getGlobalConfig("user.name")
    }
  } catch (err) {
    username = ""
  }
  outputChannel.appendLine("Configured user name: " + username)
  let repoName = vscode.workspace.workspaceFolders![0].name
  console.log(repoName)
  let username2 = ""
  let results = repoName.match(
    new RegExp(`${Config.settings.config.slug}-(.+?)$`, "i")
  )
  if (results !== null) {
    username2 = results[1]
  } else {
    outputChannel.appendLine("Result didn't match")
  }
  if (username.toLowerCase().localeCompare(username2.toLowerCase()) !== 0) {
    username = username2
    git.repositories[0].setConfig("user.name", username)
    changed = true
  }
  updateConfigVars({ user: { githubID: username } })
  console.log("Retrieving github id...")
  let json = await axios.get('https://api.github.com/users/' + username2)
  console.log(json)
  let { id, email } = json.data
  if (email === null) {
    userEmail = `${id}+${username}@users.noreply.github.com`
  } else {
    userEmail = email
  }
  if (
    (userEmail !== undefined && !emailRe.test(userEmail)) ||
    userEmail === "knight@ucf.edu" ||
    userEmail === ""
  ) {
    try {
      userEmail = await git.repositories[0].getConfig("user.email")
      outputChannel.appendLine("Configured repo email: " + userEmail)
    } catch (err) {
      console.log("No email configured ")
      try {
        userEmail = await git.repositories[0].getGlobalConfig("user.email")
        outputChannel.appendLine("Configured global email: " + userEmail)
      } catch (err) {
        outputChannel.appendLine("Error, no global email configured" + err)
        while (userEmail === "" || userEmail === "knight@ucf.edu" || !emailRe.test(email)) {
          userEmail = (await vscode.window.showInputBox({
            prompt:
              "Enter an Email associated with your GitHub account.\n You can enter the private one or a public one located here: https://github.com/settings/emails",
          })) as string
        }
      }
    }
  }
  git.repositories[0].setConfig("user.email", userEmail)
  return username
}

export async function getExtensionVersion(system: string) {
  let version = "0.0.0"
  switch (system) {
    case 'gaim.vsix':

      version = vscode.extensions.getExtension("ucf-gaim.gaim")?.packageJSON
        .version
      outputChannel.appendLine(`${system}: v${version}`)
      try {
        await updateConfigVars({ user: { versions: { "gaim.vsix": version } } })
      } catch (e) {
        console.log("Error: " + e)
        outputChannel.appendLine("Error: " + e)
      }
      break
    case 'gaim.unity':
      try {
        let packageFile = readFileSync(path.join(vscode.workspace.workspaceFolders![0].uri.fsPath, "Packages", "GaIM", "package.json")).toString()
        let pkg = JSON.parse(packageFile)
        version = pkg.version
        await updateConfigVars({ user: { versions: { "gaim.unity": version } } })
        outputChannel.appendLine(`${system}: v${version}`)
      } catch (e) {
        console.log("Error: " + e)
        outputChannel.appendLine("Error: " + e)
      }
      break
  }
  VersionBar.updateStatusBarItem()
  return version
}

export function generateSessionId(length: number) {
  var date = Date.now()
  let result = date.toString() + "-"
  const characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789"
  var charactersLength = characters.length
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
  }
  return result
}
export function updateSettingsFile() {
  let settingsFile = path.join(
    vscode.workspace.workspaceFolders?.[0].uri.fsPath!,
    ".vscode",
    "settings.json"
  )
  if (!existsSync(settingsFile)) {
    mkdirSync(
      path.join(vscode.workspace.workspaceFolders?.[0].uri.fsPath!, ".vscode"),
      { recursive: true }
    )
    writeFileSync(settingsFile, "")
  }
  let json = readFileSync(settingsFile).toString()
  json = json
    .split("\n")
    .filter((line) => !/\s*\/\//.test(line))
    .join("\n")
  let regex = /\,(?!\s*?[\{\[\"\'\w])/g
  json = json.replace(regex, "") // remove all trailing commas
  let jsonObject
  console.log(json)
  try {
    jsonObject = JSON.parse(json)
  } catch (error) {
    console.log(error)
    jsonObject = {}
  }
  jsonObject["gitdoc.autoCommitDelay"] = 15000
  jsonObject["gitdoc.autoPush"] = "onCommit"
  jsonObject["gitdoc.autoPushDelay"] = 10000
  jsonObject["gitdoc.commitValidationLevel"] = "none"
  jsonObject["gitdoc.enabled"] = true
  jsonObject["git.enabled"] = true
  jsonObject["editor.formatOnPaste"] = true
  jsonObject["editor.formatOnSave"] = true
  jsonObject["eslint.format.enable"] = true
  jsonObject["files.autoSave"] = "afterDelay"
  jsonObject["git.autoStash"] = true
  writeFileSync(settingsFile, JSON.stringify(jsonObject, undefined, "  "))
}
